
.sequencerrow {
  width:95%;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack:center;
      justify-content:center;
}

.stepbutton {
  cursor: pointer;
  width: 40px;
  height: 40px;
  background-color: #9e9e9e;
  margin: 5px;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
}

.lightoff {
  display: block;
  position: absolute;
  background: #4a2b2b;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  top: 3px;
  left: 13px;
  border: 2px #212121 solid;
}

.lighton {
  display: block;
  position: absolute;
  background: #f44336 ;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  top: 3px;
  left: 13px;
  border: 2px #212121 solid;
}

.stepbutton:nth-of-type(n):nth-of-type(-n+4) {
  background-color: #f44336 ;
}

.stepbutton:nth-of-type(n+5):nth-of-type(-n+8) {
  background-color: #fdd835;
}

.stepbutton:nth-of-type(n+9):nth-of-type(-n+12) {
  background-color: #0277bd;
}

.stepbutton:nth-of-type(n+13):nth-of-type(-n+16) {
  background-color: #9e9e9e;
}


